* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}

a,
a:hover,
a:active {
  text-decoration: none;
  cursor: pointer;
}

ul {
  margin: 0;
  padding: 0;
}

button:active,
button::-moz-focus-inner {
  border: 0;
  outline: none;
}

svg {
  cursor: pointer;
}
